.pink {
  background: linear-gradient(to top, #3366cc 0%, #66ffff 100%);
  padding: 12px;
  border-radius: 16px;
}
.orange {
  background: linear-gradient(to top, #ff6600 0%, #ffff00 100%);
  padding: 12px;
  border-radius: 16px;
}
.green {
  background: linear-gradient(to top, #006600 0%, #00ff00 100%);
  padding: 12px;
  border-radius: 16px;
}
.SNU1 {
  display: flex;
  justify-content: space-around;
}
.darah {
  border: 1px solid black;
  height: 65vh;
  width: 98vw;
  margin-inline: 20px;
  margin-top: 20px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 60px;
  cursor: pointer;
}
.ROW {
  display: flex;
  align-items: center;
  flex-direction: column;
}
