.Xocontainer {
  background: linear-gradient(to top left, #ff0000 0%, #660033 100%);
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 20px;
}
.Xooo {
  height: 80%;
  aspect-ratio: 1;
  border-radius: 15px;
  background-color: #000000;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
.box {
  /* height: ; */
  height: 30%;
  width: 30%;
  background-color: #ffc700;
  transition: 0.3s;
  font-size: 96px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.box:hover {
  background-color: lightpink;
}
.Winner {
  font-size: 90px;
  color: #ebf400;
}
.EZ {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
