.container {
  display: flex;
  justify-content: space-around;
}

.zez {
  width: 200px;
  border-radius: 10px;
  height: 300px;
  object-fit: cover;
}

.searchbar {
  text-align: center;
}
.searchbar input {
  padding: 12px;
  border-radius: 8px;
  width: 30%;
}
.searchbar input:hover {
  -webkit-box-shadow: 10px 10px 76px 0px rgba(130, 69, 130, 1);
  -moz-box-shadow: 10px 10px 76px 0px rgba(130, 69, 130, 1);
  box-shadow: 10px 10px 76px 0px rgba(130, 69, 130, 1);
}
.movie .xurxn {
  transition: 0.3s;
}
.movie .xurxn:hover {
  scale: 1.05;
}
