.aguulga {
  background: linear-gradient(to top right, #0066cc 0%, #00ffff 100%);
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.aguulga h1 {
  margin: 0;
}
.aguulga button {
  background-color: blue;
  color: white;
  padding-inline: 12px;
  padding-top: 6px;
  padding-bottom: 6px;
  transition: 0.3s;
  margin-top: 5px;
}
.aguulga button:hover {
  background-color: darkblue;
}

.aguulga input {
  padding: 6px;
  border-radius: 8px;
}
