.tovchnuud {
  display: flex;
  background-color: azure;
  justify-content: center;
  padding: 12px;
  gap: 10px;
  border-radius: 20px;
}
.tovchnuud button {
  background: linear-gradient(to top, #33ccff 0%, #ff33cc 100%);
  color: white;
  padding: 10px;
  padding-inline: 16px;

  border-radius: 12px;
  -webkit-box-shadow: -1px 2px 52px 7px rgba(0, 0, 0, 0.55);
  -moz-box-shadow: -1px 2px 52px 7px rgba(0, 0, 0, 0.55);
  box-shadow: -1px 2px 52px 7px rgba(0, 0, 0, 0.55);

  transition: 0.3s;
}

.content {
  padding: 20px;
}
.tovchnuud {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #c6ffdd; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #f7797d,
    #fbd786,
    #c6ffdd
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #f7797d,
    #fbd786,
    #c6ffdd
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.tovchnuud h3 {
  border-radius: 10px;
  font-size: 32px;
  color: white;
  padding: 8px;
}

.tovchnuud .menu {
  gap: 16px;
  display: flex;
}
